
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
hr{margin:10px 0
}
.dashed-border{border:2px dashed #bfbfbf
}
.solid-border{border:2px solid #bfbfbf
}
.fade-enter-active,.fade-leave-active{transition:opacity 250ms
}
.fade-enter,.fade-leave-active{opacity:0
}
.preview-wrapper{background:white;width:100%;padding:1em
}
.row{display:-ms-flexbox;display:flex;border:2px dashed transparent;transition:all 200ms ease
}
.row:hover.show-container{border-color:#e6e6e6;transition:all 200ms ease
}
.default .quote{opacity:0.7;color:#3273dc;transition:all 200ms ease
}
.default .by{opacity:0.7;color:#bfbfbf;margin-top:10px;transition:all 200ms ease
}
.dropzone{background:white;z-index:0;margin:0;height:10px;border:1px dashed transparent;border-color:transparent;transition:border-color 200ms ease
}
.dropzone.default{height:auto;padding:1em;font-size:20px;border-width:2px
}
.dropzone.default.drop-target .quote,.dropzone.default.drop-target .by{opacity:0
}
.dropzone.fill{height:auto;margin:0 10px;z-index:0;min-height:10px
}
.dropzone.highlighted{border-color:#21C230;transition:border-color 200ms ease
}
.drop-active{border:1px dashed #1179C9;margin:0;transition:all 200ms ease
}
.drop-target{background:#1179C9;border-color:transparent;z-index:1;transition:background 200ms ease, border-color 200ms ease
}



