
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
hr{margin:10px 0
}
.dashed-border{border:2px dashed #bfbfbf
}
.solid-border{border:2px solid #bfbfbf
}
.fade-enter-active,.fade-leave-active{transition:opacity 250ms
}
.fade-enter,.fade-leave-active{opacity:0
}
.component-container .space{display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.component-container .space .hint{color:#bfbfbf;opacity:0;transition:all 300ms ease
}
.component-container:hover .hint{opacity:1;transition:all 300ms ease
}
.being-edited .space .hint{opacity:1;transition:all 300ms ease
}



