
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
hr{margin:10px 0
}
.dashed-border{border:2px dashed #bfbfbf
}
.solid-border{border:2px solid #bfbfbf
}
.fade-enter-active,.fade-leave-active{transition:opacity 250ms
}
.fade-enter,.fade-leave-active{opacity:0
}
.column{margin:20px;height:70px;font-size:17px;background:whitesmoke;padding:0px
}
.column.draggable{z-index:9999;padding:0
}
.element{display:-ms-flexbox;display:flex;-ms-flex-flow:row;flex-flow:row;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;height:100%;width:100%;background-color:whitesmoke;border:2px dashed #bfbfbf;transition:all 300ms ease
}
.element:hover{cursor:-webkit-grab;cursor:grab;border-color:#1179C9;transition:border-color 300ms ease
}
.element.moving{cursor:-webkit-grab;cursor:grab
}
.can-drop{z-index:1;transition:background 300ms
}
.can-drop .element{background-color:#21C230;border-color:transparent;color:white;transition:all 300ms ease
}



