
@keyframes spinAround{
from{transform:rotate(0deg)
}
to{transform:rotate(359deg)
}
}
hr{margin:10px 0
}
.dashed-border{border:2px dashed #bfbfbf
}
.solid-border{border:2px solid #bfbfbf
}
.fade-enter-active,.fade-leave-active{transition:opacity 250ms
}
.fade-enter,.fade-leave-active{opacity:0
}
.fill{width:100%;display:inline-block;z-index:1
}
.component-container{position:relative;width:100%;border:2px dashed transparent;transition:border-color 200ms ease
}
.component-container:hover{border-color:#bfbfbf;transition:border-color 200ms ease
}
.component-container:hover .icons{opacity:1;transition:opacity 200ms ease
}
.component-container.resizing{border-color:#bfbfbf;transition:border-color 200ms ease
}
.component-container.resizing .icons{opacity:0;transition:all 300ms ease
}
.highlighted .component-container{border-color:#21C230;transition:border-color 200ms ease
}
.being-edited .component-container{border-color:#ff3860;transition:border-color 200ms ease
}
.icons{position:absolute;display:-ms-flexbox;display:flex;-ms-flex-flow:row;flex-flow:row;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;top:1px;right:5px;color:#1179C9;padding:4px;z-index:9999;background:white;opacity:0;transition:opacity 200ms ease
}
.icons .fa{-ms-flex:1;flex:1;font-size:17px;margin:0 5px
}
.icons .fa:hover{cursor:pointer;color:#38A9F9
}
.paddingT30{padding-top:30px
}
.paddingT15{padding-top:15px
}
.paddingT5{padding-top:5px
}
.paddingT0{padding-top:0
}
.paddingB30{padding-bottom:30px
}
.paddingB15{padding-bottom:15px
}
.paddingB5{padding-bottom:5px
}
.paddingB0{padding-bottom:0
}



